

._buildon-dialog {
    top:0;
    position:fixed;
    width:100%;
    height:100vh;
    background-color: rgba(0,0,0,0.50);
    z-index: 999999;
    display:grid;
    grid-template-columns: 1fr;
    padding:30px;
}

._buildon-dialog ._buildon-dialog-panel {
    background-color:white;
    align-self:center;
    border-radius: 4px;
    animation-name:scaleUp;
    animation-fill-mode: forwards;
    animation-duration: 300ms;
    justify-self: center;
}

._buildon-dialog ._buildon-dialog-image {
    width:100%;
    padding-top:200px;
    margin:0;
    border-radius: 4px 4px 0 0;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat
}

._buildon-paragraph {
    text-indent: 20px;
    text-align: justify;
    font-size: 10pt
}

._buildon-numbered-list {
    font-size:10pt;
}

._buildon-donate {
    width:100%;
    padding:10px;
    background:linear-gradient(to right,#1ca7ec,#1f2f98);
    border:none;
    border-top:1px solid #f2f2f2;
    font-weight: bold;
    border-radius: 0 0 4px 4px;
    color:white;
}


._buildon-dialog-close {
    width:100%;
    float:left;
    background-color: rgba(0,0,0,0.3);
    padding:10px;
    color:#eaeaea;
    text-align: right;
    border-radius: 3px 3px 0 0 ;
}

._buildon-donate-panel ul {
    margin:0;
    padding:0;
    list-style: none;
}

._buildon-donate-panel ul li {
    padding: 10px;
    display:flex;
    align-items: center;
    transform: all 300ms;
}

._buildon-donate-panel ul li img{
    width: 35px;
    margin-right: 10px
}

._buildon-donate-panel ul li:not(:last-child) {
    border-bottom: 1px solid rgb(245, 245, 245);
}

._buildon-donate-panel ul li:first-child, .list-also {
    border-top: 1px solid rgb(245, 245, 245);
}

.list-label {
    display:block;
    padding: 10px;
    color: rgb(116, 116, 116);
    font-size: 10pt;
    background-color: rgb(241, 241, 241);
}

.send-package-panel {
    padding: 10px;
    border:1px solid rgb(241, 241, 241);
    margin-top: 10px;
    margin-bottom:10px;
    border-radius: 3px;
}

.send-package-panel h1 {
    margin:0
}

.send-package-panel p{
    font-size: 9pt
}

.send-package-panel p details {
    margin-top: 10px
}

.send-package-panel p details summary {
    font-weight: bold;
}

@keyframes scaleUp {
    from {
        transform: translateY(-40px);
        opacity: 0
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}
